import DefaultList from '@/components/content/defaultlist/DefaultList.vue'

export default {
	components: {
		DefaultList
	},
	data() {
		return {
			
		}
	},
	methods: {
		//跳转到DefModify
		toDefModify(type, value) {
			const modifyObj = {
				type: type,
				value: value
			}
			uni.navigateTo({
				url: `/components/content/defmodify/DefModify?modifyObj=${modifyObj}`
			})
		},
		
		//跳转到GroupMembers
		toGroupMembers() {
			uni.navigateTo({
				url: `/components/content/session/sessionSon/GroupMembers`
			})
		},
		
		//跳转到ChooseFriend
		toChooseFriend() {
			uni.navigateTo({
				url: `/components/content/chooseFriend/ChooseFriend`
			})
		},
		//设置消息提醒
		handleRemind() {
			uni.showActionSheet({
				itemList: ['提醒所有消息', '只接收不提醒', '不提醒任何消息', '取消'],
				success: res => {
					console.log(res)
					if(res.tapIndex){
						switch (res.tapIndex) {
							case 0:
								console.log('提醒所有消息')
								break;
							case 1:
								console.log('只接收不提醒')
								break;
							case 2:
								console.log('不提醒任何消息')
								break;
							case 3:
								console.log('取消')
								break;
						}
					}
				}
			})
		},
		
		//退出该群
		signOutGroup() {
			uni.showModal({
				title: '提示',
				content: '确定退出该群?',
				success: res => {
					if(res.confirm) {
						uni.showToast({
							title: '已退出该群',
							icon: 'none',
							mask: true,
							success: res => {
								setTimeout(() => {
									uni.navigateBack({
										delta: 2
									})
								}, 1000)
							}
						})
					}
				}
			})
		}
		
		
		
	}
}
